.card {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
}

.card--socials {
    display: flex;
    flex-direction: column;
    gap: 2.5rem;
}

.card--detail {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    gap: 2.5rem;
}

.detail--social {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 0.5625rem;
}

.card--open {
    min-height: 0.5rem;
    max-height: 1rem;
}

.card--link a {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 5px;
    font-size: 25px;
    text-decoration: none;
    color: #313131;
}

.card--qrcode {
    min-height: 3rem;
    max-height: 5.3125rem;
}

.card--name {
    color: #313131;
}

.background-glass {
    background: linear-gradient(120.15deg, #D9D9D9 0%, rgba(217, 217, 217, 0) 100%);
    backdrop-filter: blur(27px);
    /* Note: backdrop-filter has minimal browser support */
    border-radius: 20px;
    padding: 20px;
}

.background {
    background: #D9D9D9;
    border-radius: 20px;
    padding: 20px;
}
